.wrapper {
    position: relative;
    width: 420px;
    height: 450px;
    background: #899d9b;
    border: 2px solid rgba(255, 255, 255, 0.1);

    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    color: white;
    display: flex;
    align-items: center;
    overflow: hidden;
    transition: height 0.2s ease;
}

.wrapper.active {
    height: 520px;
}

.wrapper .form-box {
    width: 100%;
    padding: 40px;
}

.wrapper .form-box.login {
    transition: translate 0.18s ease;
    translate: 0;
}

.wrapper.active .form-box.login {
    transition: none;
    translate: -400px;
}

.wrapper .form-box.register {
    position: absolute;
    transition: none;
    translate: 400px;
}

.wrapper.active .form-box.register {
    transition: translate 0.18s ease;
    translate: 0;
}

form h1 {
    font-size: 36px;
    text-align: center;
}

form .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}
.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: none;
    outline: none;
    border: 2px solid rgba(255, 255, 255, 0.1);
    border-radius: 40px;
    font-size: 16px;
    color: white;
    padding: 20px 45px 20px 20px;
}
.input-box input::placeholder {
    color: white;
}
.icon {
    position: absolute;
    right: 20px;
    top: 50%;
    translate: 0 -50%;
    font-size: 16px;
}
form .remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 14.5px;
    margin: 45px 0 15px;
}

.remember-forgot label input {
    accent-color: white;
    margin-right: 4px;
}
.remember-forgot a {
    color: white;
    text-decoration: none;
}
.remember-forgot a:hover {
    text-decoration: underline;
}
form button {
    width: 100%;
    height: 45px;
    background: white;
    border: none;
    outline: none;
    border-radius: 40px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    font-size: 16px;
    color: #58362d;
    font-weight: 700;
}
form .register-link {
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}

.register-link p span.link-button,
.remember-forgot span.link-button {
    background: none;
    border: none;
    color: white;
    text-decoration: none;
    font-weight: 600;
    cursor: pointer;
    padding: 0;
}

.register-link p span.link-button:hover,
.remember-forgot span.link-button:hover {
    text-decoration: underline;
}
.text-danger {
    color: rgb(112, 10, 10);
    font-size: 12px;
}
