.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #faf4e8e2;
    overflow: hidden;
    overflow-y: auto;
    transition: opacity 0.3s;
    z-index: 999;
}

.modal-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
}

.modal-content {
    position: relative;
    margin: 20px;
    width: 100%;
    max-width: 500px;
    border-radius: 20px;
    background-color: #859e9b87;
    color: #58362d;
    padding: 40px 20px 20px 20px;
    transform: translateY(-50px);
    transition: opacity 0.3s, transform 0.3s;
}
.modal-content p {
    padding: 20px 20px 20px 0;
    text-align: justify;
    text-indent: 20px;
    line-height: 1.6;
    margin: 0;
    font-family: 'NunitoSans';
    font-size: 18px;
}

.modal-closebtn {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 30px;
    height: 30px;
    padding: 0;
    background-color: #faf4e8c4;
    border-radius: 25px;
    cursor: pointer;
}

.modal-showbtn {
    cursor: pointer;
    position: relative;
    margin: 0 auto;
}
.modal-showbtn img {
    width: 40px;
}
